import React from "react"
import {  graphql } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Img from "gatsby-image";

export const query = graphql`
query {
    file(relativePath: { eq: "aries-foodie-wechat.jpg" }) {
        childImageSharp {
            fluid(quality: 90) {
                ...GatsbyImageSharpFluid
            }
        }
    }
}`;

class OpenWeChat extends React.Component {
    render() {
        return <a className="btn btn-primary" href="weixin://">Open WeChat</a>
    }
}

export default function AriesFoodieWeChatPage({ data }) {
    return (
        <Layout>
            <SEO title="AriesFoodieWeChat" />
            <Row className="justify-content-center">
                <Col lg={6} md={9}>
                    <Img fluid={data.file.childImageSharp.fluid} />
                </Col>
            </Row>
            <p className="text-center">
                <OpenWeChat />
            </p>
        </Layout>
    );
}
